@import url(https://fonts.googleapis.com/css?family=Raleway:200);
body {
  margin: 0;
  font-family: "Raleway", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

body,
html,
#root,
.App {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  text-align: center;
  display: flex;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.blurred {
  -webkit-filter: blur(10px);
          filter: blur(10px);
}

.body-container {
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(/dashboard-app/static/media/landscape.2fba0b7b.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.dashboard-body-container{display:flex;justify-content:space-around;flex-wrap:wrap;width:100%;height:80%;justify-content:center;align-items:center}

.current-weather-block{font-family:-apple-system, BlinkMacSystemFont;color:white;position:absolute;right:30px;top:30px}.current-weather-block .current-weather-body{display:flex;flex-direction:column;height:auto}.current-weather-block .current-weather-body .icon{margin-bottom:5px}.current-weather-block .current-weather-body .temperature,.current-weather-block .current-weather-body .description{font-size:16pt;font-weight:bold}

.today-training-block{font-family:-apple-system, BlinkMacSystemFont;color:white;position:absolute;left:20px;top:30px}.today-training-block .today-training-body{display:flex;flex-direction:row;justify-content:space-evenly}.today-training-block .today-training-body .today-training-am,.today-training-block .today-training-body .today-training-pm{padding-left:20px;padding-right:20px}.today-training-block .today-training-body .today-training-am{border-right:1px solid lightgrey}.today-training-block .today-training-body .icon{margin-bottom:5px}.today-training-block .today-training-body .exercise{font-size:16pt;font-weight:bold}

.todo-list-container{font-family:-apple-system, BlinkMacSystemFont;color:white;height:300px;font-size:20pt}.todo-list-container .todo-list-header{font-size:30pt;font-weight:bold;margin-bottom:20px}.todo-list-container .todo-list-body{display:flex;flex-direction:column;height:auto}.todo-list-container .todo-list-body .form-control:disabled{background-color:#fff}.todo-list-container .todo-list-body .todo-input{width:100%;outline:none;border:none;background:none;color:white}.todo-list-container .todo-list-body .input-box{border-bottom:1px solid white;margin-bottom:30px}.todo-list-container .todo-list-body .add-todo-button{float:right}.todo-list-container .todo-list-body .task-list{cursor:pointer;display:flex;flex-direction:row}.todo-list-container .todo-list-body .completed{text-decoration:line-through}.todo-list-container .todo-list-body .icon{margin-top:10px;margin-bottom:20px}.todo-list-container .todo-list-body .temperature,.todo-list-container .todo-list-body .description{font-size:22pt;margin-bottom:10px}

.block-header-container{border-bottom:1px solid lightgrey;margin-bottom:10px;width:100%;height:50px;display:flex;justify-content:center;align-items:center;font-weight:bold}

.custom-checkbox{display:flex}.custom-checkbox .trash{height:20px;float:right;cursor:pointer}.custom-checkbox .container{display:block;position:relative;padding-left:35px;margin-bottom:12px;cursor:pointer;font-size:22px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.custom-checkbox .checked{text-decoration:line-through}.custom-checkbox .container input{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.custom-checkbox .checkmark{position:absolute;top:0;left:0;height:25px;width:25px;background-color:#eee}.custom-checkbox .container:hover input ~ .checkmark{background-color:#ccc}.custom-checkbox .container input:checked ~ .checkmark{background-color:#2196f3}.custom-checkbox .checkmark:after{content:"";position:absolute;display:none}.custom-checkbox .container input:checked ~ .checkmark:after{display:block}.custom-checkbox .container .checkmark:after{left:9px;top:5px;width:5px;height:10px;border:solid white;border-width:0 3px 3px 0;transform:rotate(45deg)}

.hamburger-menu-open,.hamburger-menu-closed{border-right:1px solid lightgray;height:100%;width:300px;background-color:#fff;z-index:99}.hamburger-menu-open{position:absolute;left:0;top:0}.hamburger-menu-open img{position:absolute;left:15px;top:10px;cursor:pointer}.hamburger-menu-closed{display:none}

